
import { defineComponent } from "vue";

import { modelWrapper, emptyExample } from "@/common";

import AddRemove from "@/components/AddRemove.vue";
import MultiLine from "@/components/MultiLine.vue";

export default defineComponent({
    components: {
        AddRemove,
        MultiLine,
    },
    props: {
        number: String,
        modelValue: Array,
    },
    methods: {
        blur($event: any) {
            let text = $event.target.value;

            if (text && (text.startsWith('"') || text.endsWith('"'))) {
                $event.target.value = text.replace(/^"+|"+$/g, "");
            }
        },
    },
    data() {
        return {
            empty: emptyExample(),
        };
    },
    setup(props, { emit }) {
        return {
            model: modelWrapper(props, emit),
        };
    },
});
