import { computed } from "vue";

export function modelWrapper(props: any, emit: any, name = 'modelValue') {
    return computed({
        get: () => props[name],
        set: (value) => emit(`update:${name}`, value)
    })
}

export function isAlphabet($event: any) {
    const char = String.fromCharCode($event.keyCode);

    if (!/^[A-Za-z]+$/.test(char)) $event.preventDefault();
}

export function getNextFibonacci(last: number) {
    const next = last * (1 + Math.sqrt(5)) / 2;

    return Math.round(next);
}

export const initData: any = () => {
    return {
        meanings: [emptyMeaning()],
    };
};

export const emptyMeaning: any = () => {
    return {
        definitions: [emptyDefinition()],
    };
};

export const emptyDefinition: any = () => {
    return {
        examples: [emptyExample()],
    };
};

export const emptyExample: any = () => {
    return {};
};
