export function getWord(word: string): Promise<any> {
    return firebase.firestore().collection(collection).doc(word).get();
}

export function saveWord(doc: any): Promise<void> {
    return firebase.firestore().collection(collection).doc(doc.word).set(doc);
}

export function removeWord(word: string): Promise<void> {
    return firebase.firestore().collection(collection).doc(word).delete();
}

export function getReview(): Promise<any> {
    const today = moment().format("YYYY-MM-DD");

    return firebase.firestore().collection(collection).where("next_review", "<=", today).orderBy("next_review").orderBy("save_date").orderBy("save_time").get();
}

export function saveReview(word: string, data: any): Promise<void> {
    return firebase.firestore().collection(collection).doc(word).update(data);
}

export function getList(type: string): Promise<any> {
    if (type == "today") {
        const today = moment().format("YYYY-MM-DD");

        return firebase.firestore().collection(collection).where("save_date", "==", today).orderBy("word").get();
    }
    else if (type == "ytd") {
        const ytd = moment().add(-1, "days").format("YYYY-MM-DD");

        return firebase.firestore().collection(collection).where("save_date", "==", ytd).orderBy("word").get();
    }
    else if (type == "last20")
        return firebase.firestore().collection(collection).orderBy("save_date", "desc").orderBy("save_time", "desc").limit(20).get();
    else // search
        return firebase.firestore().collection(collection).orderBy("word").get();
}
