
import { defineComponent } from "vue";

export default defineComponent({
    props: {
        modelValue: Array,
        index: Number,
        empty: Object,
    },
    methods: {
        add(index: number) {
            if (this.modelValue) {
                let newValue = [...this.modelValue];
                newValue.splice(
                    index + 1,
                    0,
                    JSON.parse(JSON.stringify(this.empty))
                );
                // spread ... and Object.assign() not work for copying empty

                this.$emit("update:modelValue", newValue);
            }
        },
        remove(index: number) {
            if (this.modelValue && this.modelValue.length > 1) {
                let newValue = [...this.modelValue];
                newValue.splice(index, 1);

                this.$emit("update:modelValue", newValue);
            }
        },
    },
});
