
import { defineComponent } from "vue";

import { isAlphabet } from "@/common";

export default defineComponent({
    props: {
        modelValue: Array,
    },
    methods: {
        remove($event: any) {
            let removeValue: string[] = $event.value;

            if (this.modelValue && removeValue.length) {
                let index = this.modelValue.indexOf(removeValue[0]);

                if (index != -1) {
                    let newValue = [...this.modelValue];
                    newValue.splice(index, 1);

                    this.$emit("update:modelValue", newValue);
                }
            }
        },
        isAlphabet($event: any) {
            isAlphabet($event);
        },
        clickChip(e: any) {
            let target = e.target;

            if (
                target.className == "p-chips-token" ||
                target.className == "p-chips-token-label"
            ) {
                this.$emit("clickChip", target.innerText);
            }
        },
    },
});
