
import { defineComponent } from "vue";

import { modelWrapper, emptyDefinition } from "@/common";

import AddRemove from "@/components/AddRemove.vue";
import MultiLine from "@/components/MultiLine.vue";
import Example from "@/components/Example.vue";

export default defineComponent({
    components: {
        AddRemove,
        MultiLine,
        Example,
    },
    props: {
        number: String,
        modelValue: Array,
    },
    methods: {
        blur($event: any) {
            let text = $event.target.value;

            if (text && text.endsWith(".")) {
                $event.target.value = text.replace(/\.+$/, "");
            }
        },
    },
    data() {
        return {
            empty: emptyDefinition(),
        };
    },
    setup(props, { emit }) {
        return {
            model: modelWrapper(props, emit),
        };
    },
});
